@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Mukta:wght@400;500;600;700&display=swap");
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mukta", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  background: white;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}
.App {
  padding: 1rem;
  width: 90%;
  margin: auto;
}
p,
small {
  padding: 0;
  margin: 0;
}
.shopBlock {
  display: flex;
  justify-content: space-between;
  min-height: 100vh;
}
